// fonts
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

body {
  font-family: 'Poppins', sans-serif;
}
.navbar-dark {
  background-color: #0E0E0E;
  color: #ffffff;
  .navbar-toggler {
    border: none;
  }
}
.offcanvas-end {
  border-left: none;
}
.py-160 {
  padding-top: 160px;
  padding-bottom: 160px;
}
.banner {
  background-color: #0E0E0E;
  padding-top: 90px;
  margin-top: -90px;
  .card {
    border: none;
    border-radius: none ;
  }
  .carousel {
    margin-bottom: -90px;
    .carousel-caption {
      text-align: left;
      bottom: 30%;
      h2 {
      font-size: 60px;
      }
    }
    .carousel-indicators {
      button {
      height: 20px;
      background-color: transparent;
      color: #ffffff;
      }
    }
  }
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
